@import 'kanban-board.scss';

.project-board {
    position: relative;

    ::-webkit-scrollbar {
        width: 6px;
        height: 6px;
    }

    ::-webkit-scrollbar-thumb {
        background: rgba(127, 127, 127, 0.5);
        border-radius: 3px;
    }

    .kanban-board {
        overflow-x: auto;
    }

    .kanban-board,
    .kanban-column > .cards-container {
        scrollbar-width: thin;
    }

    .kanban-column {
        min-width: 300px;

        .tippy-content {
            padding: 0px;
        }

        > .header {
            background-color: var(--brand-primary);
            display: flex;
            padding: 4px 8px;

            > .title {
                flex: 1 0 auto;
                padding: 0px;
                background-color: initial;
                color: white;
            }

            > .menu {
                cursor: pointer;
                margin-left: 6px;
                color: white;
            }
        }
    }

    .alert-backdrop {
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        background-color: rgba(128, 128, 128, 0.5);
    }

    .alert-message {
        position: absolute;
        left: 50%;
        top: 50%;
        max-width: 50%;
        transform: translate(-50%, -50%);
    }

    .alert-content {
        background-color: white;
        border-radius: 8px;
        box-shadow: 4px 4px 8px gray;
    }

    .alert-title {
        padding: 8px 12px;
        font-weight: bold;
        border-bottom: 1px solid lightgray;
    }

    .alert-body {
        padding: 16px 12px;
    }
}

.kanban-card {
    .card-content {
        position: relative;

        .menu {
            cursor: pointer;
            float: right;
            margin: 4px 8px 0px 4px;
        }

        a.card-link {
            color: var(--text-color);
        }
    }

    .card-cancelled,
    .card-completed {
        opacity: 0.5;
    }

    .card-completed .card-title .fa {
        color: var(--brand-success);
    }

    .action-line {
        margin-top: 8px;
        margin-bottom: 4px;
    }

    .avatar-list {
        margin-right: 8px;
        display: inline-flex;
        flex-direction: row-reverse;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: center;
        vertical-align: middle;

        .avatar {
            border: 2px solid white;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            display: inline-block;
            margin-left: -8px;
        }

        .avatar:last-child {
            margin-left: 0px;
        }

        .avatar-photo {
            background-size: cover;
        }

        .avatar-more {
            background-color: #ccc;
            font-size: 14px;
            text-align: center;
            padding-top: 6px;
            color: #000;
        }
    }

    .due-date {
        font-size: 0.8em;
        vertical-align: middle;
        display: inline-block;
        background-color: rgba(0,0,0,0.05);
        padding: 4px 8px;
        border-radius: 2px;
    }
}

