.kanban-board {
    display: flex;
}

.kanban-column {
    margin-right: 12px;
    min-width: 240px;
    flex: 1 0;
    width: 100%;
    display: flex;
    flex-direction: column;

    &:last-child {
        margin-right: 0px;
    }

    .title {
        text-align: center;
        background-color: #009ce3;
        color: white;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        padding: 4px 8px;
        text-transform: uppercase;
        flex: 0 0 auto;
    }

    .cards-container {
        flex: 1 0;
        border: 1px solid #ddd;
        border-top: none;
        overflow-y: auto;
    }

    .cards {
        min-height: 88px;
        padding: 12px;
        width: 100%;
    }
}


.kanban-card {
    margin-bottom: 12px;
    border-radius: 3px;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.15);
    overflow: hidden;

    .card-content {
        display: block;
        background-color: #fff;
        padding: 6px 10px;
        min-height: 50px;
    }

    &:last-child {
        margin-bottom: 0px;
    }
}
